      // se o pedido for maior q 5k
      if (this.valor_pedido < 0) {
        soma += this.serasa_score;
      }

      soma = Math.round(soma);

      if (soma >= 12) {
        this.resultado = "Liberar crédito";
      }
      if (soma == 11) {
        this.resultado = "50% de entrada";
      }

      if (soma < 11) {
        this.resultado = "Somente á vista";
      }

      if (soma) return soma;
